 * @Description: 指定人员查看指定岗位人员数据
 * @Autor: lwq 
 * @Date: 2020-08-15 14:17:17
 * @LastEditors: yd
 * @LastEditTime: 2020-09-12 18:28:09
 * @FilePath: \mrc-base-server-view\src\views\data-access-strategy\modules\ViewDataOfEmployeesInTheSamePosition\index.vue
-->
<template>
  <v-card>
    <v-card-text class="px-7">
      <!-- 头顶 -->
      <showDataAccessHeader @add='add'
                            :showAdd='!readonly'
                            :label="$t('指定人员查看指定岗位人员数据')"
                            :readonly='readonly'
                            v-model="dataobj.mrbase_sys_data_strategy_specified_staff_topost_is_used" />
      <!-- 中间内容 -->
      <dataCard v-for="(item,index) in list"
                class="mb-7"
                :readonly='readonly'
                :index=index
                :key='item.uuid'
                :list='item'
                @edit='edit'
                @deleteItem='deleteItem'>
        <template #default='{item}'>
          <v-row>
            <v-col cols="12">
              <mrc_ess_autoCompleteStaff v-model="item.staff_uuid"
                                         :label="$t('员工姓名')"
                                         :multiple='true'></mrc_ess_autoCompleteStaff>
            </v-col>
            <v-col cols="12">
              <mrc_ess_autoCompletePosition v-model="item.specified_post_uuids"
                                            :label="$t('被查看岗位')"
                                            :multiple='true'></mrc_ess_autoCompletePosition>
            </v-col>
            <v-col cols="12">
              <mrc_ess_autoCompleteStaff v-model="item.exclude_staff_ids"
                                         item-key='staff_id'
                                         :label="$t('排除人员')"
                                         :multiple='true'></mrc_ess_autoCompleteStaff>
            </v-col>
            <v-col cols="12">
              <tree v-model="item.function_uuids"
                    :readonly='true'></tree>
            </v-col>
          </v-row>
        </template>
      </dataCard>
      <!-- 弹出框 -->
    </v-card-text>
    <dialogModule v-model="open"
                  :title="$t('指定人员查看指定岗位人员数据')"
                  :paramsData='currentItem'
                  @getData='getData' />
    <mrc_ess_submitBtn @success='success'
                       v-if="!readonly"
                       :cancelText="$store.state.uuid?'取消':$t('label_The_previous_step')"
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template> 

<script>
import { required } from 'vuelidate/lib/validators'
import showDataAccessHeader from '../showDataAccessHeader'
import dialogModule from './dialogModule'
import dataCard from '../dataCard'
import tree from '../tree'
import {
  listData,
  getData,
  updateData,
  addData,
  delData
} from '@/service/api/data-access-strategy'
export default {
  components: { showDataAccessHeader, dialogModule, dataCard, tree },
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      open: false,
      params: null,
      currentItem: {},
      currentIndex: {},
      dataobj: {
        mrbase_sys_data_strategy_specified_staff_topost_is_used: '0'
      },
      list: [],
      deleteList: [] //删除的每一项
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  async mounted () {
    if (this.$store.state.uuid) {//修改
      await this.getList()
    }
    else {//新增
    }
  },
  methods: {
    async getList () {
      if (this.$store.state.uuid) {
        const res = await getData({ org_level_uuid: this.$store.state.uuid, date: 'mrbase_sys_data_strategy_specified_staff_topost' })
        if (res.code == 200) {
          this.list = res.mrbase_sys_data_strategy_specified_staff_topost ? res.mrbase_sys_data_strategy_specified_staff_topost : []
          this.dataobj = res.data
        }
      }
      else {

      }
    },
    add () {
      this.currentItem = Object.assign({})
      this.currentIndex = Object.assign({})
      this.open = true
    },
    getData (val, type) { // 0新增 1修改
      this.params = val
      console.log(type);
      if (type == 1) {
        this.$set(this.list, this.currentIndex, Object.assign(this.currentItem, val))
      }
      else {
        this.list.push(val)
      }
    },
    edit (item, index) {  //修改
      console.log(item, index);
      this.currentItem = item
      this.currentIndex = index
      this.open = true
    },
    deleteItem (item, index) { //删除
      this.currentItem = item
      this.currentIndex = index
      if (item.uuid) {
        this.deleteList.push({ ...item, type: 'del' })
      }
      this.list.splice(index, 1)
    },
    async success () {
      if (this.$store.state.uuid) {//修改
        let arr = [...this.list, ...this.deleteList].map(item => {
          return { ...item, strategy_uuid: this.dataobj.uuid }
        })
        const res = await updateData({
          '~table~': 'mrbase_sys_data_strategy_specified_staff_topost',
          mrbase_sys_data_strategy_specified_staff_topost_is_used: this.dataobj.mrbase_sys_data_strategy_specified_staff_topost_is_used,
          mrbase_sys_data_strategy_specified_staff_topost: arr
        })
        if (res.code == 200) {
          this.$Message({ text: this.$t('修改成功') })
          await this.getList()
          this.$emit('updateSuccess')
        }
        else {
          this.$Message({ text: this.$t(res.msg), color: 'rgb(0, 0, 0, 0.38)' })
        }
      }
      else {//新增
        this.$emit('success')
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  },
};
</script>

<style scoped>
</style>
